import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'

import Vmain from '@/components/Vmain'
import Vnote from '@/components/Vnote'
import Vconnect from '@/components/Vconnect'
//const Vmain = resolve => require(['./Vmain.vue'], resolve);
//const Vnote = resolve => require(['./Vnote.vue'], resolve);

Vue.use(Router)

const router=new Router({
  routes: [
    {
      path: '/',
      name: 'Vmain',
      component: Vmain
    },
     {
      path: '/note',
      name: 'Vnote',
      component: Vnote
    },
    {
      path: '/connect',
      name: 'Vconnect',
      component: Vconnect
    }
  ]
})

router.afterEach(route => {

	document.title="Welcome to "+route.name;

})

export default router;


