<template>
	<div class="col-md-3">
		<!-- <button class="btn btn-danger" @click='addNote'>添加一条数据</button> -->
		<!--<Vnotebtn type='warning' @noteBtn='noteHandler'></Vnotebtn>-->
		<div class="panel panel-default">
			<div class="panel-heading">
				<h3 class="panel-title">我的笔记列表</h3>
			</div>
			<div class="panel-body">
				<ul class="list-unstyled">

					<Vnoteitem v-for='(ele,index) in getNotes' :note='ele' :key="index" :index="index"></Vnoteitem>
				</ul>
			</div>
			<!--<div class="sr-only">{{this.getNotes}}</div>-->
		</div>
	</div>
</template>

<script>
	import Vnoteitem from './Vnoteitem.vue'
	import Vnotebtn from './Vnotebtn.vue'
	export default {
		name: 'Vnotelist',
		data() {
			return {

			}
		},
		methods: {
			noteHandler() {
				// 但是  vuex 不推荐咱们这样直接插数据  注意 千万不要在这理直接修改
				// this.$store.state.notes.push({id:5,title:'我是二级标题',content:'哈哈哈,你们想的太简单了'})

				// 第一个参数 定义的事件的名字  第二个参数 数据
				this.$store.commit('addNote2', {
					id: 5,
					title: '我是二级标题',
					content: '哈哈哈,你们想的太简单了'
				})
			},

		},
		components: {
			Vnoteitem,
			Vnotebtn
		},
		computed: {
			getNotes() {
				return this.$store.state.notes
			}
		},
	}
</script>

<style scoped>
	ul,
	ol,
	li {
		list-style: none;
	}
</style>


