<template>

	<div class="container-fluid">

		<div class="row">

			<!-- 笔记列表 -->
			<Vnotelist></Vnotelist>

			<!--markdown编辑器-->
			<VnoteEditor></VnoteEditor>

		</div>

	</div>

</template>

<script>
	import Vnotelist from './Vnotelist'
	import VnoteEditor from './VnoteEditor'
	export default {
		name: 'Vnote',
		data() {
			return {

			}
		},
		components: {
			Vnotelist,
			VnoteEditor

		},
		mounted() {
			this.$store.commit('note_get');
		},
		created() {
			this.$store.commit('switch_class',this.$router.currentRoute.name);
		}
	}
</script>

<style>

</style>


