// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';

import Vuex from 'vuex';

// 1.引入vuex

//2.使用vuex
Vue.use(Vuex);
//3.创建store(仓库)

import axios from 'axios';
import qs from 'qs';
//axios.post('/req/vnote', {
//		firstName: 'Fred',
//		lastName: 'Flintstone'
//	})
//	.then(function(response) {
//		console.log(response);
//	})
//	.catch(function(error) {
//		console.log(error);
//	});

const store = new Vuex.Store({
	state: {
		notes: [],
		opinions: [],
		note: {
			title: '',
			content: '',
			result: ''
		},
		isSaved: false,
		tagActive: '',
		test: ''
	},
	getters:{

	},
	//进化 要想修改state里面的内容 必须通过mutations定义的方法
	mutations: {
		// 载荷
		//submit
		note_submit(state, obj) {
			axios.post('http://localhost:3010/commend/add/', qs.stringify(obj))
				.then(function(response) {
					if(response.data.error == 0) {
						obj.odate = new Date();
						obj.Id = response.data.Id;
						state.notes.push(obj);

						state.note = {
							title: '',
							content: '',
						};
						state.isSaved = false;

					} else
					;

				})
				.catch(function(error) {
					console.log(error);
				});
		},
		//get
		note_get(state, obj) {

			axios.post('http://localhost:3010/commend/get/')
				.then(function(response) {
					if(response.data.error == 0) {
						state.notes = response.data.data;
					} else
					;
				})
				.catch(function(error) {
					console.log(error);
				});
		},
		//get
		note_del(state, obj) {
			console.log(state.notes, obj)

			axios.post('http://localhost:3010/commend/del/', qs.stringify(obj))
				.then(function(response) {
					if(response.data.error == 0) {
						let length = state.notes.length;
						for(let i = 0; i < length; i++) {
							if(state.notes[i].id == obj.id) {
								state.notes.splice(i, 1);
								break;
							}
							state.note = {
								title: '',
								content: '',
							};
							state.isSaved = false;
						}
					} else
					;

				})
				.catch(function(error) {
					console.log(error);
				});
		},
		//change
		note_change(state, obj) {

			axios.post('http://localhost:3010/commend/upd/', qs.stringify(obj))
				.then(function(response) {

					if(response.data.error == 0) {

						for(let i = 0; i < length; i++) {

							if(state.notes[i].Id == obj.Id) {

								state.note.splice(i, 1, obj);
								//								state.notes[i] = obj;
								//								state.notes = [...state.notes];

								break;
							}
						}
					} else
					;
					state.note = {
						title: '',
						content: '',
					};
					state.isSaved = false;
				})
				.catch(function(error) {
					console.log(error);
				});
		},
		// not act
		connect_submit(state, obj) {
			obj.id = new Date().getTime();
			obj.oDate = new Date();
			state.opinions.push(obj);
		},
		// click noteitem to change
		noteitem_select(state, obj) {

			state.note = obj.note;
			console.log(state.note);

			state.isSaved = true;
		},
		//clear the obj
		clear(state, obj) {
			console.log(state, obj);
			state.note = {
				title: '',
				content: '',
			};
			state.isSaved = false;
		},
		//change the tag
		switch_class(state, str) {
			state.tagActive = str;
		},
		add1(state, obj) {
			state.test = obj.content;
			console.log(state.test)

		},

	},
	actions: {

	}
})

Vue.config.productionTip = false
import router from './router';
/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	store,
	template: '<App/>',
	components: {
		App
	}
})


