<template>
	<div class="container-fluid">
		<div class="row">
			<div class="col-md-6">
				<div class="panel panel-success">
					<div class="panel-heading">Connect to me</div>
					<div class="panel-body connect1">


						<img class="center-block " src="../assets/angel2.png" alt="" />


						<p class="bg-success text-left"><span class="col-md-3">Name</span>Chen Xin</p>
						<p class="bg-info text-left"><span class="col-md-3">Age</span>22</p>
						<p class="bg-success text-left"><span class="col-md-3">Email</span>tedachenx56@126.com</p>
						<p class="bg-info text-left"><span class="col-md-3">Telephone</span>18526400751</p>
					</div>
				</div>
			</div>
			<div class="col-md-6">
				<div class="panel panel-success">
					<div class="panel-heading">Send your message or opinion</div>
					<div class="panel-body">
						<form class="form-horizontal">
							<div class="form-group">
								<label for="Vconnect_name" class="col-sm-2 control-label">Name*</label>
								<div class="col-sm-10">
									<input type="text" v-model="name" class="form-control" id="Vconnect_name" placeholder="Name" />
								</div>
							</div>
							<div class="form-group">
								<label for="Vconnect_title" class="col-sm-2 control-label">Title*</label>
								<div class="col-sm-10">
									<input type="text" v-model="title" class="form-control" id="Vconnect_title" placeholder="Title" />
								</div>
							</div>
							<div class="form-group">
								<label for="Vconnect_content" class="col-sm-2 control-label">Conetent*</label>
								<div class="col-sm-10">
									<textarea v-model="content" class="form-control" id="Vconnect_content" rows="7" placeholder="Conetent"></textarea>
								</div>
							</div>
							<div class="form-group">
								<label for="Vconnect_connection" class="col-sm-2 control-label">Connect Way*</label>
								<div class="col-sm-10">
									<textarea v-model="connection" class="form-control" id="Vconnect_connection" rows="3" placeholder="How to connect you"></textarea>

								</div>
							</div>
							<div class="form-group">
								<div class="col-sm-offset-2 col-sm-10">
									<!--bug-->
									<!--<a class="btn btn-default" role="button" :href="mailtourl">Send to me</a>-->
									<button class="btn btn-default" @click="submit">submit</button>
								</div>
							</div>
						</form>

					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Vconnect",
		data() {
			return {
				name: '',
				title: '',
				content: '',
				connection: ''
			}
		},
		computed: {
			mailtourl() {
				return `mailto:tedachenx56@126.com?subject=${this.title+" by "+this.name}&body=${this.content}`
			}
		},
		methods: {
			submit() {
				if(this.title == '' || this.content == '' || this.content == '' || this.connection == '') {
					alert('Something is empty')
					return;
				}
				this.$store.commit('connect_submit', {
					name: this.title,
					title: this.title,
					content: this.content,
					connection: this.connection
				})
			}
		},
		created() {
			this.$store.commit('switch_class',this.$router.currentRoute.name);
		}
	}
</script>

<style scoped>
img{
	width:50%;
	text-align: center;
	padding-bottom: 30px;
}
.connect1{
	font-size:20px
}
</style>


