<template>
		<button class="btn" :class='typeBtn' @click='btnClick'>
		<slot>按钮</slot>
	</button>
</template>

<script>
	export default{
	name:'Vnotebtn',
	data(){
		return{

		}
	},
	props:{
		type:String
	},
	methods:{
		btnClick(){
				this.$emit("noteBtn");
		}

	},
	computed:{
		typeBtn(){
			return {
				'btn-info':this.type === 'info' ? true: false,
				'btn-danger':this.type === 'danger' ? true: false,
				'btn-success':this.type === 'success' ? true: false,
				'btn-warning':this.type === 'warning' ? true: false,

			}
		}
	}
}
</script>

<style>
</style>


