<template>
	<div class="col-md-9">
		<!-- <button class="btn btn-danger" @click='addNote'>添加一条数据</button> -->
		<!--<Vnotebtn type='warning' @noteBtn='noteHandler'></Vnotebtn>-->
		<div class="row">
			<div class="col-md-6">
				<div class="panel panel-default">
					<div class="panel-heading">
						<h3 class="panel-title">editor</h3>
					</div>
					<div class="panel-body">
						<form>
							<div class="form-group">
								<label for="vnoteeditor_title">title:</label>
								<input v-model="title" type="text" class="form-control" id="vnoteeditor_title" placeholder="Please input title">
							</div>

							<div class="form-group">
								<label for="vnoteeditor_conetnt">content:</label>
								<textarea class="form-control" rows="8" v-model="content" id="vnoteeditor_conetnt" placeholder="Please input content"></textarea>
							</div>
							<button class="btn btn-default" type="button" @click="submit">{{isSaved?"保存":"提交"}}</button>
							<button class="btn btn-default" type="button" @click="clear">清空</button>
							<button v-if="isSaved" class="btn btn-default" type="button" @click="del">删除</button>
							<div class="sr-only">{{note}}</div>
							<!--<button class="btn btn-default" type="button" @click="test">test</button>-->
							<!--<button class="btn btn-default" type="button" @click="test1">test</button>-->
						</form>
					</div>
				</div>
			</div>
			<div class="col-md-6">
				<div class="panel panel-default">
					<div class="panel-heading">
						<h3 class="panel-title">After Parsing by <em>marked</em></h3>
					</div>
					<div class="panel-body vnoteeditor_result" v-html="result">

					</div>
				</div>
			</div>
		</div>
	</div>

</template>

<script>
	//	import Vnotebtn from './Vnotebtn.vue'
	import marked from 'marked'
	import { mapGetters } from 'vuex'

	export default {
		name: 'VnoteEditor',
		data() {
			return {
				title: '',
				content: '',

			}
		},
		props: {},
		computed: {
			result() {
				return marked(this.content);
			},
			isSaved() {
				return this.$store.state.isSaved;
			},
			note() {
				let it = this.$store.state.note;
				for(let name in it) {
					if(name == "result")
						continue;
					this[name] = it[name];
				}
//				return this.$store.state.note.title + this.$store.state.note.content;
			},
		},
		methods: {
			del() {
				this.$store.commit('note_del', {
					Id: this.$store.state.note.Id
				});

			},
			clear() {
				this.$store.commit('clear', {
					title: '',
					content: ''
				});
			},

			submit() {
				console.log(112)

				if(this.title == '' || this.content == '') {
					alert('title or content is empty')
					return;
				}
				if(this.$store.state.isSaved) {
					this.$store.commit('note_change', {
						title: this.title,
						content: this.content,
						result: this.result,
						Id: this.$store.state.note.Id
					})
				} else {
					this.$store.commit('note_submit', {
						title: this.title,
						content: this.content,
						result: this.result,
					})
				}

			},
//			test() {
//				this.$store.commit('add1');
//			},
//			test1() {
//				this.$store.commit('note_get')
//			}
		}
	}
</script>

<style>
	.vnoteeditor_result {
		height: 50%
	}
</style>


