<template>
	<li :index="index" @click="select">
		<h3>{{note.title}}</h3>
		<p v-html="note.result"></p>
		<!--<span>{{note.id}}</span>-->
		<span>{{note.oDate}}</span>

	</li>
</template>

<script>
	export default {
		name: 'Vnoteitem',
		data() {
			return {

			}
		},
		props: {
			note: Object,
			index: Number,
		},
		methods: {
			select() {
				this.$store.commit('noteitem_select',{
					note:this.note
				})
			},
		}

	}
</script>

<style scoped>
	li {
		border-bottom: 1px solid grey;
	}
</style>


